import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <article>
        <img className={"person_photo"} src={'../../selivanov/selivanov_3.jpg'} alt={""} />
        <div>
          <div className={"person_name"}>Артём Селиванов</div>
        </div>
      <div className={"textBlock"}>
        <p>На&nbsp;самоизоляции мне пока скучать не&nbsp;приходится. Наоборот, все нравится, кроме ограничений выхода на&nbsp;улицу. Я&nbsp;решил подучить английский и&nbsp;выучить ещё один дополнительный язык. Получается даже очень хорошо. А&nbsp;в&nbsp;перерывах между учёбой решил научиться ещё одному важному делу&nbsp;&mdash; приготовлению пищи. Пока тоже получается. Ещё хотел снова отрегулировать свой режим: пораньше вставать и&nbsp;пораньше ложиться&nbsp;&mdash; это, наверное, единственное, что у&nbsp;меня не&nbsp;получается на&nbsp;карантине.</p>
        <p>Раньше я&nbsp;очень любил фильм &laquo;Патрик&raquo;, нравился он&nbsp;мне своим мотивом и&nbsp;схожей с&nbsp;моей ситуацией, но&nbsp;позже любовь пропала. Сейчас пересмотрел и&nbsp;понял, что снова влюбился в&nbsp;него, всем рекомендую! Также перечитал книгу &laquo;Бизнес в&nbsp;шоколаде&raquo;, а&nbsp;сейчас дочитываю второй раз &laquo;Тонкое чувство пофигизма&raquo;, тоже всем рекомендую!</p>
        <p>В&nbsp;нашей семье добавились тёплые вечера на&nbsp;кухне со&nbsp;свечками, пледами и&nbsp;вкусным чаем. Раньше такого не&nbsp;было и&nbsp;сейчас понимаю, что этого как раз и&nbsp;не&nbsp;хватало.</p>
        <p><nobr>Из-за</nobr> сложившейся ситуации в&nbsp;стране, кончено, пришлось исключить встречи с&nbsp;друзьями. Но&nbsp;сейчас 21 век на&nbsp;дворе, и&nbsp;это не&nbsp;повод грустить, поэтому мы&nbsp;созваниваемся по&nbsp;видео конференциям или просто по&nbsp;телефону, ведь половину моих знакомых уехали за&nbsp;город, чтобы пережить это тяжёлое время и&nbsp;интернет там не&nbsp;поддерживается.</p>
        <p>Дистанционное обучение идёт своим чередом. Часто происходят забавные истории на&nbsp;наших онлайн уроках. То&nbsp;<nobr>кто-нибудь</nobr> забудет звук выключить и&nbsp;пойдёт на&nbsp;кухню кушать, то&nbsp;<nobr>кто-нибудь</nobr> телевизор смотрит. Наши учителя <nobr>из-за</nobr> этого не&nbsp;стали строже, а&nbsp;мы&nbsp;&mdash; ученики <nobr>из-за</nobr> этого стали менее послушно себя вести. Хорошие оценки стало получать проще, мне всегда нравился формат теста, там, <nobr>где-нибудь</nobr> есть подсказка.</p>
        <p>Мне нравится, как я&nbsp;выгляжу на&nbsp;экране своего девайса. Если не&nbsp;нравиться себе самому в&nbsp;объективе, то&nbsp;нет даже смысла туда смотреть.</p>
        <p>Я&nbsp;думаю, для меня окончание карантина не&nbsp;станет <nobr>чем-то</nobr> сверхъестественным, но&nbsp;в&nbsp;первый день я&nbsp;хочу собрать всех своих друзей <nobr>где-нибудь</nobr> в&nbsp;уютном кафе за&nbsp;чашечкой кофе и&nbsp;обсудить, как у&nbsp;кого прошёл карантин и&nbsp;что изменилось за&nbsp;это время.</p>
        <p>Мне&nbsp;бы хотелось посетить London и&nbsp;Германию. Очень уж&nbsp;интересная у&nbsp;них культура!</p>
        <p>Думаю, что за&nbsp;это время люди убедятся, что спорт очень важен в&nbsp;жизни, будут более тщательно выбирать продукты питания: ведь сейчас здоровье на&nbsp;первом месте. Думаю, люди осознают, что такое свобода и&nbsp;оценят, как на&nbsp;карантине им&nbsp;помогали родственники и&nbsp;государство.</p>
        <p>Если предложат, то&nbsp;я&nbsp;выберу суперспособность невидимки. Я&nbsp;думаю, это очевидно!</p>
      </div>
    </article>
  </Layout>
)

export default IndexPage



